* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  /* Colors - 차분한 네이비 (remote1, 2와 동일) */
  --color-primary: #1E3A5F;
  --color-secondary: #64748B;
  --color-accent: #0EA5E9;
  --color-bg: #FFFFFF;
  --color-bg-secondary: #F8FAFC;
  --color-text: #1E3A5F;
  --color-text-secondary: #64748B;
  --color-text-muted: #94A3B8;
  --color-border: #E2E8F0;

  /* Typography */
  --font-sans: 'Pretendard', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;

  /* Spacing */
  --container-max: 1100px;
  --section-gap: 100px;

  /* Border Radius */
  --radius-sm: 8px;
  --radius-md: 12px;
  --radius-lg: 16px;
  --radius-xl: 20px;
  --radius-2xl: 24px;
}

body {
  font-family: var(--font-sans);
  color: var(--color-text);
  background: var(--color-bg);
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
}

a {
  text-decoration: none;
  color: inherit;
}

/* ===== Animations ===== */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInBlur {
  from {
    opacity: 0;
    filter: blur(10px);
  }
  to {
    opacity: 1;
    filter: blur(0);
  }
}

@keyframes slideInLeft {
  from {
    opacity: 0;
    transform: translateX(-30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes scaleIn {
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

.animate-fade-in {
  animation: fadeIn 0.8s ease-out forwards;
}

.animate-fade-blur {
  animation: fadeInBlur 1s ease-out forwards;
}

.animate-slide-left {
  animation: slideInLeft 0.8s ease-out forwards;
}

.animate-scale {
  animation: scaleIn 0.6s ease-out forwards;
}

/* Stagger delays */
.delay-1 { animation-delay: 0.1s; }
.delay-2 { animation-delay: 0.2s; }
.delay-3 { animation-delay: 0.3s; }
.delay-4 { animation-delay: 0.4s; }
.delay-5 { animation-delay: 0.5s; }

/* Scroll Animation */
.animate-on-scroll {
  opacity: 0;
  transform: translateY(30px);
  transition: opacity 0.6s ease-out, transform 0.6s ease-out;
}

.animate-on-scroll.animate-visible {
  opacity: 1;
  transform: translateY(0);
}

.animate-on-scroll.delay-1 { transition-delay: 0.1s; }
.animate-on-scroll.delay-2 { transition-delay: 0.2s; }
.animate-on-scroll.delay-3 { transition-delay: 0.3s; }
.animate-on-scroll.delay-4 { transition-delay: 0.4s; }
.animate-on-scroll.delay-5 { transition-delay: 0.5s; }

/* ===== Layout ===== */
.container {
  max-width: var(--container-max);
  margin: 0 auto;
  padding: 0 24px;
}

.section {
  padding: var(--section-gap) 0;
}

.section-alt {
  background: var(--color-bg-secondary);
}

.section-header {
  margin-bottom: 48px;
  text-align: center;
}

.section-label {
  font-size: 14px;
  font-weight: 600;
  color: var(--color-accent);
  margin-bottom: 8px;
  letter-spacing: 0.5px;
}

.section-title {
  font-size: 32px;
  font-weight: 700;
  line-height: 1.3;
  color: var(--color-primary);
}

/* ===== Featured Project Cards (포트폴리오) ===== */
.project-list {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.project-card.featured {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 40px;
  align-items: center;
  padding: 36px;
  background: white;
  border-radius: var(--radius-2xl);
  overflow: hidden;
  transition: all 0.4s ease;
  cursor: pointer;
  box-shadow: 0 4px 24px rgba(30, 58, 95, 0.06);
  position: relative;
}

.project-card.featured::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, var(--color-primary), var(--color-accent));
  transform: scaleX(0);
  transition: transform 0.4s ease;
}

.project-card.featured:hover {
  transform: translateY(-8px);
  box-shadow: 0 24px 64px rgba(30, 58, 95, 0.12);
}

.project-card.featured:hover::before {
  transform: scaleX(1);
}

.project-card.featured .project-thumbnail {
  width: 100%;
  height: 200px;
  background: linear-gradient(135deg, var(--color-bg-secondary) 0%, #E0F2FE 100%);
  border-radius: var(--radius-lg);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 56px;
  transition: transform 0.4s ease;
}

.project-card.featured:hover .project-thumbnail {
  transform: scale(1.02);
}

.project-card.featured .project-title {
  font-size: 24px;
  font-weight: 700;
  line-height: 1.4;
  margin-bottom: 12px;
  color: var(--color-primary);
  transition: color 0.3s ease;
}

.project-card.featured:hover .project-title {
  color: var(--color-accent);
}

.project-card.featured .project-desc {
  font-size: 15px;
  color: var(--color-text-secondary);
  line-height: 1.6;
  margin-bottom: 20px;
}

.project-card.featured .project-tag {
  padding: 6px 12px;
  font-size: 12px;
  font-weight: 500;
  background: var(--color-bg-secondary);
  border-radius: var(--radius-sm);
  color: var(--color-text-secondary);
  transition: all 0.3s ease;
}

.project-card.featured .project-tag:hover {
  background: var(--color-accent);
  color: white;
}

.project-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.project-links {
  display: flex;
  gap: 12px;
  margin-top: 20px;
}

.project-link {
  padding: 10px 20px;
  font-size: 14px;
  font-weight: 600;
  border-radius: var(--radius-md);
  transition: all 0.3s ease;
}

.project-link.primary {
  background: var(--color-primary);
  color: white;
}

.project-link.primary:hover {
  background: var(--color-accent);
  transform: translateY(-2px);
  box-shadow: 0 8px 24px rgba(14, 165, 233, 0.3);
}

.project-link.secondary {
  background: transparent;
  color: var(--color-primary);
  border: 1.5px solid var(--color-border);
}

.project-link.secondary:hover {
  border-color: var(--color-accent);
  color: var(--color-accent);
}

/* ===== Small Project Cards (토이/클론) ===== */
.project-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 24px;
}

.project-card-small {
  padding: 32px;
  background: white;
  border-radius: var(--radius-xl);
  transition: all 0.4s ease;
  cursor: pointer;
  box-shadow: 0 4px 24px rgba(30, 58, 95, 0.06);
  position: relative;
  overflow: hidden;
}

.project-card-small::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: var(--color-accent);
  transform: scaleX(0);
  transition: transform 0.4s ease;
}

.section-alt .project-card-small {
  background: white;
}

.project-card-small:hover {
  transform: translateY(-8px);
  box-shadow: 0 20px 60px rgba(30, 58, 95, 0.12);
}

.project-card-small:hover::before {
  transform: scaleX(1);
}

.project-thumbnail-small {
  width: 100%;
  height: 140px;
  background: linear-gradient(135deg, var(--color-bg-secondary) 0%, #E0F2FE 100%);
  border-radius: var(--radius-lg);
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 42px;
  transition: transform 0.4s ease;
}

.project-card-small:hover .project-thumbnail-small {
  transform: scale(1.05);
}

.section-alt .project-thumbnail-small {
  background: linear-gradient(135deg, var(--color-bg-secondary) 0%, #E0F2FE 100%);
}

.project-title-small {
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 10px;
  color: var(--color-primary);
  transition: color 0.3s ease;
}

.project-card-small:hover .project-title-small {
  color: var(--color-accent);
}

.project-desc-small {
  font-size: 14px;
  color: var(--color-text-secondary);
  line-height: 1.6;
  margin-bottom: 16px;
}

.project-tag-small {
  padding: 5px 10px;
  font-size: 11px;
  font-weight: 500;
  background: var(--color-bg-secondary);
  border-radius: var(--radius-sm);
  color: var(--color-text-secondary);
  transition: all 0.3s ease;
}

.project-tag-small:hover {
  background: var(--color-accent);
  color: white;
}

.section-alt .project-tag-small {
  background: var(--color-bg-secondary);
}

/* ===== Scroll Top Button ===== */
.scroll-top-btn {
  position: fixed;
  right: 32px;
  bottom: 32px;
  width: 48px;
  height: 48px;
  background: var(--color-primary);
  color: white;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  z-index: 999;
  opacity: 0;
  visibility: hidden;
  transform: translateY(20px);
  transition: all 0.3s ease;
  box-shadow: 0 4px 16px rgba(30, 58, 95, 0.2);
  display: flex;
  align-items: center;
  justify-content: center;
}

.scroll-top-btn.visible {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

.scroll-top-btn:hover {
  background: var(--color-accent);
  transform: translateY(-4px);
  box-shadow: 0 8px 24px rgba(14, 165, 233, 0.3);
}

/* ===== Responsive ===== */
@media (max-width: 768px) {
  :root {
    --section-gap: 60px;
  }

  .section-title {
    font-size: 26px;
  }

  .project-card.featured {
    grid-template-columns: 1fr;
    gap: 24px;
  }

  .project-card.featured .project-thumbnail {
    height: 160px;
  }

  .project-grid {
    grid-template-columns: 1fr;
  }
}
