;IAC1B,SAAS;EACX;;EAEA;IACE,aAAa;EACf;;EAEA;IACE,0BAA0B;EAC5B;AACF","sourcesContent":["/* ===== CSS Reset & Variables ===== */\n* {\n  margin: 0;\n  padding: 0;\n  box-sizing: border-box;\n}\n\n:root {\n  /* Colors - 차분한 네이비 (remote1, 2와 동일) */\n  --color-primary: #1E3A5F;\n  --color-secondary: #64748B;\n  --color-accent: #0EA5E9;\n  --color-bg: #FFFFFF;\n  --color-bg-secondary: #F8FAFC;\n  --color-text: #1E3A5F;\n  --color-text-secondary: #64748B;\n  --color-text-muted: #94A3B8;\n  --color-border: #E2E8F0;\n\n  /* Typography */\n  --font-sans: 'Pretendard', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;\n\n  /* Spacing */\n  --container-max: 1100px;\n  --section-gap: 100px;\n\n  /* Border Radius */\n  --radius-sm: 8px;\n  --radius-md: 12px;\n  --radius-lg: 16px;\n  --radius-xl: 20px;\n  --radius-2xl: 24px;\n}\n\nbody {\n  font-family: var(--font-sans);\n  color: var(--color-text);\n  background: var(--color-bg);\n  line-height: 1.6;\n  -webkit-font-smoothing: antialiased;\n}\n\na {\n  text-decoration: none;\n  color: inherit;\n}\n\n/* ===== Animations ===== */\n@keyframes fadeIn {\n  from {\n    opacity: 0;\n    transform: translateY(20px);\n  }\n  to {\n    opacity: 1;\n    transform: translateY(0);\n  }\n}\n\n@keyframes fadeInBlur {\n  from {\n    opacity: 0;\n    filter: blur(10px);\n  }\n  to {\n    opacity: 1;\n    filter: blur(0);\n  }\n}\n\n@keyframes slideInLeft {\n  from {\n    opacity: 0;\n    transform: translateX(-30px);\n  }\n  to {\n    opacity: 1;\n    transform: translateX(0);\n  }\n}\n\n@keyframes scaleIn {\n  from {\n    opacity: 0;\n    transform: scale(0.95);\n  }\n  to {\n    opacity: 1;\n    transform: scale(1);\n  }\n}\n\n.animate-fade-in {\n  animation: fadeIn 0.8s ease-out forwards;\n}\n\n.animate-fade-blur {\n  animation: fadeInBlur 1s ease-out forwards;\n}\n\n.animate-slide-left {\n  animation: slideInLeft 0.8s ease-out forwards;\n}\n\n.animate-scale {\n  animation: scaleIn 0.6s ease-out forwards;\n}\n\n/* Stagger delays */\n.delay-1 { animation-delay: 0.1s; }\n.delay-2 { animation-delay: 0.2s; }\n.delay-3 { animation-delay: 0.3s; }\n.delay-4 { animation-delay: 0.4s; }\n.delay-5 { animation-delay: 0.5s; }\n\n/* Scroll Animation */\n.animate-on-scroll {\n  opacity: 0;\n  transform: translateY(30px);\n  transition: opacity 0.6s ease-out, transform 0.6s ease-out;\n}\n\n.animate-on-scroll.animate-visible {\n  opacity: 1;\n  transform: translateY(0);\n}\n\n.animate-on-scroll.delay-1 { transition-delay: 0.1s; }\n.animate-on-scroll.delay-2 { transition-delay: 0.2s; }\n.animate-on-scroll.delay-3 { transition-delay: 0.3s; }\n.animate-on-scroll.delay-4 { transition-delay: 0.4s; }\n.animate-on-scroll.delay-5 { transition-delay: 0.5s; }\n\n/* ===== Layout ===== */\n.container {\n  max-width: var(--container-max);\n  margin: 0 auto;\n  padding: 0 24px;\n}\n\n.section {\n  padding: var(--section-gap) 0;\n}\n\n.section-alt {\n  background: var(--color-bg-secondary);\n}\n\n.section-header {\n  margin-bottom: 48px;\n  text-align: center;\n}\n\n.section-label {\n  font-size: 14px;\n  font-weight: 600;\n  color: var(--color-accent);\n  margin-bottom: 8px;\n  letter-spacing: 0.5px;\n}\n\n.section-title {\n  font-size: 32px;\n  font-weight: 700;\n  line-height: 1.3;\n  color: var(--color-primary);\n}\n\n/* ===== Featured Project Cards (포트폴리오) ===== */\n.project-list {\n  display: flex;\n  flex-direction: column;\n  gap: 24px;\n}\n\n.project-card.featured {\n  display: grid;\n  grid-template-columns: 1fr 1fr;\n  gap: 40px;\n  align-items: center;\n  padding: 36px;\n  background: white;\n  border-radius: var(--radius-2xl);\n  overflow: hidden;\n  transition: all 0.4s ease;\n  cursor: pointer;\n  box-shadow: 0 4px 24px rgba(30, 58, 95, 0.06);\n  position: relative;\n}\n\n.project-card.featured::before {\n  content: '';\n  position: absolute;\n  top: 0;\n  left: 0;\n  right: 0;\n  height: 3px;\n  background: linear-gradient(90deg, var(--color-primary), var(--color-accent));\n  transform: scaleX(0);\n  transition: transform 0.4s ease;\n}\n\n.project-card.featured:hover {\n  transform: translateY(-8px);\n  box-shadow: 0 24px 64px rgba(30, 58, 95, 0.12);\n}\n\n.project-card.featured:hover::before {\n  transform: scaleX(1);\n}\n\n.project-card.featured .project-thumbnail {\n  width: 100%;\n  height: 200px;\n  background: linear-gradient(135deg, var(--color-bg-secondary) 0%, #E0F2FE 100%);\n  border-radius: var(--radius-lg);\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  font-size: 56px;\n  transition: transform 0.4s ease;\n}\n\n.project-card.featured:hover .project-thumbnail {\n  transform: scale(1.02);\n}\n\n.project-card.featured .project-title {\n  font-size: 24px;\n  font-weight: 700;\n  line-height: 1.4;\n  margin-bottom: 12px;\n  color: var(--color-primary);\n  transition: color 0.3s ease;\n}\n\n.project-card.featured:hover .project-title {\n  color: var(--color-accent);\n}\n\n.project-card.featured .project-desc {\n  font-size: 15px;\n  color: var(--color-text-secondary);\n  line-height: 1.6;\n  margin-bottom: 20px;\n}\n\n.project-card.featured .project-tag {\n  padding: 6px 12px;\n  font-size: 12px;\n  font-weight: 500;\n  background: var(--color-bg-secondary);\n  border-radius: var(--radius-sm);\n  color: var(--color-text-secondary);\n  transition: all 0.3s ease;\n}\n\n.project-card.featured .project-tag:hover {\n  background: var(--color-accent);\n  color: white;\n}\n\n.project-tags {\n  display: flex;\n  flex-wrap: wrap;\n  gap: 8px;\n}\n\n.project-links {\n  display: flex;\n  gap: 12px;\n  margin-top: 20px;\n}\n\n.project-link {\n  padding: 10px 20px;\n  font-size: 14px;\n  font-weight: 600;\n  border-radius: var(--radius-md);\n  transition: all 0.3s ease;\n}\n\n.project-link.primary {\n  background: var(--color-primary);\n  color: white;\n}\n\n.project-link.primary:hover {\n  background: var(--color-accent);\n  transform: translateY(-2px);\n  box-shadow: 0 8px 24px rgba(14, 165, 233, 0.3);\n}\n\n.project-link.secondary {\n  background: transparent;\n  color: var(--color-primary);\n  border: 1.5px solid var(--color-border);\n}\n\n.project-link.secondary:hover {\n  border-color: var(--color-accent);\n  color: var(--color-accent);\n}\n\n/* ===== Small Project Cards (토이/클론) ===== */\n.project-grid {\n  display: grid;\n  grid-template-columns: repeat(3, 1fr);\n  gap: 24px;\n}\n\n.project-card-small {\n  padding: 32px;\n  background: white;\n  border-radius: var(--radius-xl);\n  transition: all 0.4s ease;\n  cursor: pointer;\n  box-shadow: 0 4px 24px rgba(30, 58, 95, 0.06);\n  position: relative;\n  overflow: hidden;\n}\n\n.project-card-small::before {\n  content: '';\n  position: absolute;\n  bottom: 0;\n  left: 0;\n  right: 0;\n  height: 3px;\n  background: var(--color-accent);\n  transform: scaleX(0);\n  transition: transform 0.4s ease;\n}\n\n.section-alt .project-card-small {\n  background: white;\n}\n\n.project-card-small:hover {\n  transform: translateY(-8px);\n  box-shadow: 0 20px 60px rgba(30, 58, 95, 0.12);\n}\n\n.project-card-small:hover::before {\n  transform: scaleX(1);\n}\n\n.project-thumbnail-small {\n  width: 100%;\n  height: 140px;\n  background: linear-gradient(135deg, var(--color-bg-secondary) 0%, #E0F2FE 100%);\n  border-radius: var(--radius-lg);\n  margin-bottom: 20px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  font-size: 42px;\n  transition: transform 0.4s ease;\n}\n\n.project-card-small:hover .project-thumbnail-small {\n  transform: scale(1.05);\n}\n\n.section-alt .project-thumbnail-small {\n  background: linear-gradient(135deg, var(--color-bg-secondary) 0%, #E0F2FE 100%);\n}\n\n.project-title-small {\n  font-size: 18px;\n  font-weight: 700;\n  margin-bottom: 10px;\n  color: var(--color-primary);\n  transition: color 0.3s ease;\n}\n\n.project-card-small:hover .project-title-small {\n  color: var(--color-accent);\n}\n\n.project-desc-small {\n  font-size: 14px;\n  color: var(--color-text-secondary);\n  line-height: 1.6;\n  margin-bottom: 16px;\n}\n\n.project-tag-small {\n  padding: 5px 10px;\n  font-size: 11px;\n  font-weight: 500;\n  background: var(--color-bg-secondary);\n  border-radius: var(--radius-sm);\n  color: var(--color-text-secondary);\n  transition: all 0.3s ease;\n}\n\n.project-tag-small:hover {\n  background: var(--color-accent);\n  color: white;\n}\n\n.section-alt .project-tag-small {\n  background: var(--color-bg-secondary);\n}\n\n/* ===== Scroll Top Button ===== */\n.scroll-top-btn {\n  position: fixed;\n  right: 32px;\n  bottom: 32px;\n  width: 48px;\n  height: 48px;\n  background: var(--color-primary);\n  color: white;\n  border: none;\n  border-radius: 50%;\n  cursor: pointer;\n  z-index: 999;\n  opacity: 0;\n  visibility: hidden;\n  transform: translateY(20px);\n  transition: all 0.3s ease;\n  box-shadow: 0 4px 16px rgba(30, 58, 95, 0.2);\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n\n.scroll-top-btn.visible {\n  opacity: 1;\n  visibility: visible;\n  transform: translateY(0);\n}\n\n.scroll-top-btn:hover {\n  background: var(--color-accent);\n  transform: translateY(-4px);\n  box-shadow: 0 8px 24px rgba(14, 165, 233, 0.3);\n}\n\n/* ===== Responsive ===== */\n@media (max-width: 768px) {\n  :root {\n    --section-gap: 60px;\n  }\n\n  .section-title {\n    font-size: 26px;\n  }\n\n  .project-card.featured {\n    grid-template-columns: 1fr;\n    gap: 24px;\n  }\n\n  .project-card.featured .project-thumbnail {\n    height: 160px;\n  }\n\n  .project-grid {\n    grid-template-columns: 1fr;\n  }\n}\n"],"sourceRoot":""}]);
